"use client"

import { useCart } from "@/hooks/use-cart"

export function Cart() {
  const { cart, removeFromCart, clearCart } = useCart()

  return (
    <div className="fixed bottom-6 right-6 bg-card border border-border p-4 w-72 rounded-xl shadow-xl">
      <h2 className="font-bold text-lg mb-2">Carrito</h2>

      {cart.length === 0 && (
        <p className="text-sm text-muted-foreground">El carrito está vacío</p>
      )}

      {cart.map((item) => (
        <div
          key={item.id}
          className="flex justify-between items-center py-2 border-b border-border"
        >
          <span>{item.name}</span>
          <button
            onClick={() => removeFromCart(item.id)}
            className="text-red-500 hover:text-red-700"
          >
            X
          </button>
        </div>
      ))}

      {cart.length > 0 && (
        <button
          onClick={clearCart}
          className="mt-3 w-full bg-red-500/20 text-red-500 hover:bg-red-500/30 rounded-lg p-2"
        >
          Vaciar carrito
        </button>
      )}
    </div>
  )
}
